import React, { useState } from 'react'
import { Form, Input, Card, Button, Checkbox } from 'antd'
import { useNavigate } from 'react-router-dom';
import '../index.css'

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};

const Login = () => {
   

    const navigate = useNavigate()

    const onFinish = (values) => {
        console.log('Success:', values);
        let data = localStorage.getItem("formData")
        let data2 = JSON.parse(data)
        console.log("data2", data2)
        navigate('dashboard')
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };
    return (
        <div className="site-card-border-less-wrapper" style={{display:'flex', justifyContent:'center', alignItems:'center', paddingLeft:'60px', paddingRight:'400px', paddingBottom:0}}>
            <Card title="Login" bordered={false} style={{ width: 300, alignContent: "center", border: "2px solid black" }}>
                <Form
                    name="basic"
                    labelCol={{
                        span: 8,
                    }}
                    wrapperCol={{
                        span: 16,
                    }}
                    initialValues={{
                        remember: true,
                    }}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                    autoComplete="off"
                >
                    <Form.Item
                        label="Username"
                        name="username"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your username!',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="Password"
                        name="password"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item
                        name="remember"
                        valuePropName="checked"
                        wrapperCol={{
                            offset: 8,
                            span: 16,
                        }}
                    >
                        <Checkbox>Remember me</Checkbox>
                    </Form.Item>

                    <Form.Item
                        wrapperCol={{
                            offset: 8,
                            span: 16,
                        }}
                    >
                        <Button type="primary" htmlType="submit">
                            SignIn
                        </Button>
                        Or  <a href="/register">You don't have an account? Register here.</a>
                    </Form.Item>
                </Form>


            </Card>

        </div>
    )
}

export default Login


